*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Roboto', sans-serif;   
}

/* // Scroll  */
::-webkit-scrollbar{
    width: .6rem;
    border-radius: .5rem;
    background-color: #A1A5AF;
}

::-webkit-scrollbar-thumb{
    border-radius: .5rem;
    background-color: #344bce;
}

::-webkit-scrollbar-thumb:hover{
    background-color: #5582db;
}


body{
    background-color: #201c41;
}

li{
    list-style: none;
}
a{
    text-decoration: none;
}

.loading{
    min-height: 60vh;
    display: flex;
    justify-content: center;
}

.container{
    display: grid;
    grid-template-columns: repeat(4,1fr);
    width: 90%;
    margin: 5.5rem auto;
    gap: 2.5rem;
    max-width: 1000px;

}

.movie-container {
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background: #100a1f;
    box-shadow: 4px 2px 13px 0px rgb(9 8 8 / 77%);
    position: relative;
    overflow: hidden;

}

.movie-overview{
    position: absolute;
    left: 0;
    bottom: 0;
    max-height: 100%;
    background-color: #ccc;
    opacity: .9;
    padding: 1rem;
    overflow-y: auto;
    transition: all .4s ease-in-out;
    font-size: .8rem;
    transform: translateY(100%);

}

.movie-overview h2{
    font-size: 1rem;
    margin-bottom: .4rem;
}



.movie-container:hover .movie-overview{
        transform: translateY(0%);
    
}



.title{
    font-size: .8rem;
    color: #fff;

}

.poster{
    width: 100%;
}

.pagination {
    position: fixed;
    bottom: 0;
    background-color: #100a1f;
    width: 100%;
    justify-content: center;
    gap: 1.25rem;
    padding: 0.625rem;
    display:flex;
}

.seachBar{
    position: fixed;
    top: 0;
    background-color: #100a1f;
    width: 100%;
    justify-content: space-evenly;
    z-index: 5;
    padding: .5rem;
    display:flex;
}

.seachBar-logo {
    width: 40px;
}

.form {
    background-color: transparent;
    position: relative;
}

.InputIcon{
    position: absolute;
    right: 1.1rem;
    top: .5rem;


}
.InputIcon img {
    width: 24px;
}

.inputSearch{
    border: 4px solid #201c41;
    height: 1rem;
    padding: 1rem;
    border-radius: 1rem;
    color: #4e487e;
    font-size: 14px;
}



.inputSearch:focus{
    outline: none;
    border: 4px solid #4e487e;
}

.inputSearch::placeholder{
    color: rgb(150, 149, 149)
}

.pagination button {
    cursor: pointer;
    border: none;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding: .5rem 1rem;
    background-color: #241744;
    color: #fff;
    border-radius: 100px;
    transition: .3s ease all;
    font-size: 1rem;
}

.pagination button:hover{
    background-color: #422f73;
}

.info-movie{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    padding: 1rem 1rem;

}

.tag{
    font-family:'Roboto', sans-serif; 
    font-weight: bold;   
    padding: .5rem;
    background-color: #241744;
    border-radius: 0.625rem;
}

.tag.green{
    color: rgb(0, 255, 0);
}
.tag.yellow{
    color:rgb(243, 243, 2)
}
.tag.orange{
    color: rgb(250, 164, 4);
}
.tag.red{
    color:rgb(241, 8, 8);
}
.nav{
    display: flex;
    align-items: center;
}

.nav-links{
    display: flex;
}

.nav-links li{
    margin-left: 2rem;
}
.nav-links li a {
    font-size: 1rem;
    color: #fff;
    font-family: 'Roboto', sans-serif;   
    font-weight: 500;
    font-style: italic;
    background: #241744;
    border-radius: 18px;
    padding: 8px 16px;
    transition: .3s ease all;
}

.nav-links li a:hover{
    background: #422f73;
}
.star-img{
    width: 100%;
    width: 20px;
    height: 20px;
    margin-left: 4px;
}


